.profile__image {
  position: relative;
  width: 120px;
  height: 120px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  background-size: cover;
  background-position: center;
  margin: 0 29px 0 0;
}



@media screen and (max-width: 740px) {
    .profile__image {
        margin: 0 10px 0 0;
    }
}

@media screen and (max-width: 480px) {
    .profile__image {
        margin-right: 0;
    }
}
