.popup__button {
    width: 100%;
    height: 50px;
    font-size: 18px;
    line-height: 22px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #000;
    border-radius: 2px;
    border: none;
    transition:
        visibility 0s,
        background 0.3s;
    margin-top: 48px;
}

.popup__button_disabled {
    color: #c4c4c4;
    border: 1px solid #c4c4c4;
    background: transparent;
    pointer-events: none;
}

.popup__button:hover {
    background: rgba(0, 0, 0, 0.8);
    cursor: pointer;
}

@media screen and (max-width: 568px) {
    .popup__button {
        font-size: 14px;
        line-height: 17px;
    }
}

/* Стиль для некорректного ввода  */
.popup__input_type_error {
    border-bottom: 1px solid #ff0000;
}

.popup__input:last-of-type {
    margin-bottom: 0;
}
.popup__error_visible {
    color: #ff0000;
    font-size: 12px;
    font-weight: 400;
    line-height: 1.21;
}
